.QualityAssuranceDetail{
  width: 100%;
  height: 100%;
  padding: 15px;
  background: white;
  overflow: auto;
  &-header{
    width: 100%;
    height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    &Left{
      font-size: 16px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: #333333;
      line-height: 22px;
    }
    &Right{
      display: flex;
      justify-content: flex-end;
    }
  }
  &-content{
    display: flex;
  }
  &-formItem{
    display: flex;
    width: 260px;
  }
  &-btn{
    display: flex;
    margin-top: 20px;
  }
  &-popoverContent{
    display: flex;
    flex-direction: column;
  }
  &-popoverBtn{
    display: flex;
    justify-content: flex-end;
    margin-top: 10px;
  }
  &-font{
    font-size: 16px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: rgba(51, 51, 51, 0.7);
    line-height: 22px;
  }
}

@primary-color: #1890ff;